 /* src/styles/PageLayout.css */

/* ========== Root Styling ========== */
:root {
  --primary-color: #6e8efb;
  --accent-color: #a777e3;
  --light-bg: #f9f9ff;
  --dark-bg: #1e1e1e;
  --text-light: #333333;
  --text-dark: #f0f0f0;
}

/* ========== Page Container ========== */
.page-container {
  padding: 2.5rem 2rem;
  max-width: 95%;
  margin: auto;
  background-color: #ffffff;
  color: var(--text-light);
  border-radius: 20px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.08);
  transition: background-color 0.4s ease, color 0.4s ease;
  min-height: 90vh;
}

/* ========== Header ========== */
.page-header {
  font-size: 2.5rem;
  margin-bottom: 2rem;
  font-weight: 800;
  text-align: center;
  background: linear-gradient(135deg, var(--primary-color), var(--accent-color));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: fadeInSlide 0.6s ease-out forwards;
}

/* ========== Content ========== */
.page-content {
  margin-top: 1.5rem;
  font-size: 1.25rem;
  animation: fadeIn 0.8s ease-out forwards;
}

/* ========== Page Section ========== */
.page-section {
  margin-bottom: 2.5rem;
  padding: 1.5rem;
  border-left: 5px solid var(--primary-color);
  background-color: var(--light-bg);
  border-radius: 12px;
  transition: background-color 0.3s ease;
  font-size: 1.2rem;
}

/* ========== Chart Canvas Specific ========== */
.chart-wrapper {
  width: 100%;
  height: 80vh;
  margin-top: 2rem;
}

/* ========== Dark Mode Support ========== */
body.dark-mode .page-container {
  background-color: var(--dark-bg);
  color: var(--text-dark);
  box-shadow: 0 8px 24px rgba(255, 255, 255, 0.05);
}

body.dark-mode .page-section {
  background-color: #2a2a2a;
  border-left-color: #8d61d3;
}

/* ========== Animations ========== */
@keyframes fadeInSlide {
  from {
    opacity: 0;
    transform: translateY(25px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(15px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* ========== Responsive ========== */
@media screen and (max-width: 768px) {
  .page-header {
    font-size: 2rem;
  }

  .page-content {
    font-size: 1.1rem;
  }

  .page-section {
    font-size: 1.1rem;
  }

  .chart-wrapper {
    height: 65vh;
  }
}
.export-btn {
  background-color: #6e8efb;
  color: white;
  font-size: 1rem;
  padding: 0.7rem 1.5rem;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.export-btn:hover {
  background-color: #5a74e0;
}

